<template>
  <div>
    <div class="form-container">
      <ProgressCircle :current-step="5" :total-steps="7"></ProgressCircle>
      <h2 class="form-heading">{{ $t('form.uploadPropertyphotos') }}</h2>
      <div class="form-input">
        <UploadImages
          @changed="handleImages"
          :max="20"
          :uploadMsg="$t('form.photosAndVideo')"
          fileError="images files only accepted"
          clearAll="Remove all images" />
      </div>
      <div class="uploaded-images" v-if="images.length">
        <h3>Images to be uploaded</h3>
        <b-row>
          <b-col v-for="(image, index) in images" :key="index" cols="auto">
            <img
              class="image-thumbnail"
              :src="image"
              width="150px"
              height="150px" />
            <div class="delete-image" @click="deleteImage(index)">
              <svg-icon
                name="close-white"
                width="24px"
                height="24px"></svg-icon>
            </div>
          </b-col>
        </b-row>
      </div>
    </div>

    <div class="page-actions">
      <b-button @click="goBack">{{ $t('common.back') }}</b-button>
      <b-button variant="primary" @click="goNext">
        {{ $t('common.next') }}
      </b-button>
    </div>
  </div>
</template>

<script>
import UploadImages from 'vue-upload-drop-images';
import ProgressCircle from '@/components/postingRequestForm/ProgressCircle';

export default {
  name: 'AddPropertyPage5',
  components: {
    UploadImages,
    ProgressCircle,
  },
  transition: {
    name: 'slide-fade',
    mode: 'out-in',
  },
  computed: {
    images() {
      return this.$store.state.PropertyModule.propertyImages || [];
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
    appEnv(){
      return this.$config.APP_ENV
    },
  },
  methods: {
    async handleImages(files) {
      this.$store.commit('PropertyModule/SET_PROPERTY_IMAGES', []);
      for (let i = 0; i < files.length; i++) {
        const buffer = Buffer.from(await new Response(files[i]).arrayBuffer());
        const dataUrl = `data:${files[i].type};base64,${buffer.toString(
          'base64'
        )}`;
        this.$store.commit('PropertyModule/SET_PROPERTY_IMAGES', dataUrl);
      }
    },
    deleteImage(index) {
      this.$store.commit('PropertyModule/DELETE_PROPERTY_IMAGE', index);
    },
    goNext() {
      try {
        // if (!this.images?.length) {
        //   this.$bvToast.toast('Kindly upload at least one image', {
        //     variant: 'primary',
        //     toaster: 'b-toaster-top-center',
        //     solid: true,
        //     autoHideDelay: 2000,
        //   });
        //   return;
        // }
        this.$router.push(this.localePath('/properties/add/6'));
      } catch (error) {
        this.$bvToast.toast(
          'Uploaded images are too big. Max size cannot exceed 5MB.',
          {
            variant: 'danger',
            toaster: 'b-toaster-top-center',
            solid: true,
            autoHideDelay: 3000,
          }
        );
      }
    },
    goBack() {
      this.$router.push(this.localePath('/properties/add/4'));
    },
  },
  head() {
    return {
      title: `${this.dir === "ltr" ? "Add Property | Step 5" : "إضافة عقار | الخطوة 5"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? 'Add Property | Property Images' : 'إضافة عقار | صور العقار'
        }
      ]
    }
  },
  mounted() {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth',
    });
    const currentProperty = localStorage.getItem('property') && JSON.parse(localStorage.getItem('property'));

    if (!currentProperty)
      this.$router.push(this.localePath('/properties/add/1'));
  },
};
</script>

<style lang="scss">
.form-input {
  .container {
    .mainMessage {
      text-align: center;
      font-size: 16px;
      font-weight: bold;
      color: #0078ff;
    }
    .imgsPreview {
      .imageHolder {
        .plus {
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }
}
.uploaded-images {
  padding: 16px 0;
  h3 {
    font-size: 16px;
    font-weight: bold;
    margin: 16px 0;
  }
  .row {
    .col-auto {
      img {
        position: relative;
        object-fit: cover;
        margin: 8px 0;
      }
      .delete-image {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        top: 10px;
        right: 16px;
        width: 32px;
        height: 32px;
        background: #ea293c;
        clip-path: circle();
        cursor: pointer;
      }
    }
  }
}
</style>
